<!--  -->
<template>
  <div>
    <Zi bool1="true" goback="true" data="<" con="搜索">
      <h1 @click="$router.go(-1)" slot="left"><</h1>
    </Zi>
    <div class="content">
      <div class="top">
        <input @keydown.enter="submit" placeholder="请输入商家或者美食名称" @change="bian"  v-model="value" type="text" />
        <button @click="submit">提交</button>
      </div>
      <h4  v-show="bool1==true">搜索历史</h4>

      <ul  v-show="bool1==true">
        <li v-for="(item,index) of arr" :key="index">
          {{item}}<span @click="remove(index)">x</span>
        </li>
        <li class="remove" @click="del" v-show="bool1==true">清空历史记录</li>
      </ul>
      <div class="result" v-show="bool==true">很抱歉！无搜索历史</div>
      <ol>
        <li v-for="(item,index) of list" :key="index">
          <div class="left">
            <img :src="'//elm.cangdu.org/img/'+item.image_path" alt />
          </div>
          <div class="right">
            <p>
              <span>{{item.name}}</span>
              <span>支付</span>
            </p>
            <p>月售{{item.rating_count}}单</p>
            <p class="di">
              <span>20元起送</span>/
              <span>距离{{item.distance}}</span>
            </p>
          </div>
        </li>
      </ol>
    </div>
    <Foot></Foot>
  </div>
</template>

<script>
import Zi from "../../components/header";
import Foot from "../../components/foot";
export default {
  data() {
    return {
      value: "",
      list: [],
      val:'',
      bool:false,
      arr:[],
      bool1:true
    };
  },

  components: { Zi, Foot },

  computed: {},

  mounted() {
    if(this.arr.length==0){
         this.bool1=false
       }
     if (localStorage.getItem("arr")) {
      this.arr = JSON.parse(localStorage.getItem("arr"))
    }
   if (localStorage.getItem("nameList")) {
      this.val = JSON.parse(localStorage.getItem("nameList")).geohash;
    }
  },

  created() {},


  methods: {
    remove(index){
      this.arr.splice(index,1)
       localStorage.setItem("arr",JSON.stringify(this.arr))
       if(this.arr.length==0){
         this.bool1=false
       }
    },
    bian(){
      if(this.value==""){
          this.bool=false
           this.bool1=true
      }
    },
    del(){
      if(this.value==""){
        this.bool1=false
           if (localStorage.getItem("arr")) {
           
              localStorage.removeItem("arr")
                this.arr=[]
          }
      }
    
    },
    submit() {
       this.arr.push(this.value);
       localStorage.setItem("arr",JSON.stringify(this.arr))
      var that = this;
      this.http
        .get(
          "https://elm.cangdu.org/v4/restaurants?extras[]=restaurant_activity&geohash=" +
            this.val +
            "&keyword=" +
            this.value +
            "&type=search"
        )
        .then(d => {
          console.log(d);
          if(d.data.status==0){
            this.bool=true
          }else{
  that.list = d.data;
          console.log(that.list);
          }
        
        });
    }
  }
};
</script>
<style scoped lang="scss">








.content {
  background: white;
   margin-top: 1rem;
  .remove{
    background: white;
    line-height: 0.7rem;
    height: 0.7rem;
    font-size: 0.3rem;
    text-align: center;
    color: blue;
  }
  ul{
    li{
      font-weight: bolder;
      list-style: none;
      padding-left:0.2rem;
      line-height: 0.7rem;
      height: 0.7rem;
      border-bottom: 1px solid #ccc;
      span{
        float: right;
        margin-right:0.3rem      }
    }
  }
    ol{
    li{display: flex;
    height: 1.5rem;
    border-bottom: 1px solid #ccc;

    margin-bottom: 0.3rem;
      .left{
        width: 20%;
       img{
         margin:0.2rem 0.3rem 0.3rem 0.3rem;
         width: 0.8rem;
         height: 0.8rem;
       }
      }
      .right{
        width: 80%;
        font-size: 0.2rem;
        line-height: 0.4rem;
        .di{
          border-bottom: 1px solid #ccc;
        }
      }
    }
}
  .result {
    margin: 0 auto;
    font: 0.4rem;
    color: #333;
    background-color: #fff;
    text-align: center;
    margin-top: 0.125rem;
    border: 1px solid #f2f2f2;
    line-height: 0.5rem;
  }
  h4 {
    background: #f2f2f2;
    font-size: 0.3rem;
    margin-top: 0.2rem;
    line-height: 0.6rem;
    text-indent: 0.5rem;
    font-weight: 700;
    color: #666;
  }
  .top {
   
    background: white;
    width: 93%;
    margin: 0 auto;
    padding:0.2rem 0;
    display: flex;
    input {
      border: 0.015rem solid #e4e4e4;
      font-size: 0.3rem;
      line-height: 0.5rem;
      color: #333;
      border-radius: 0.05rem;
      background-color: #f2f2f2;
      font-weight: 700;
      padding: 0 0.05rem;
    }
    button {
      border: 0.05rem solid #3190e8;
      margin-left: 0.2rem;
      font-size: 0.25rem;
      color: #fff;
      border-radius: 0.05rem;
      background-color: #3190e8;
      font-weight: 700;
      padding: 0 0.25rem;
    }
  }
}
</style>


